document.querySelector(".js-sign").addEventListener("click", function(){
  gsap.set(".forms > div", {x: "0"})
})

document.querySelector(".js-login").addEventListener("click", function(){
  gsap.set(".forms > div", {x: "-100%"})
})

document.querySelector(".back").addEventListener("click", function(){
  gsap.set(".forms > div", {x: "-100%"})
})

document.querySelector(".js-reset").addEventListener("click", function(){
  gsap.set(".forms > div", {x: "-200%"})
})